import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setLogout, setLogin } from "@/reducers/authSlice";

import { setIsModalAlert, setPopupReset } from "@/reducers/alertSlice";

import ModalAlert from "@/components/modal/ModalAlert";
import Toast from "@/components/common/Toast";

//    (ms)
const SESSION_DURATION = 6000 * 1000;

// ms " "   
const formatTime = (ms) => {
  const totalSeconds = Math.max(0, Math.floor(ms / 1000));
  const minutes = Math.floor(totalSeconds / 60);
  const seconds = totalSeconds % 60;
  return `${minutes > 0 ? `${minutes} ` : ""}${seconds}`;
};

const SessionManager = () => {
  const dispatch = useDispatch();
  const { token, expiresAt } = useSelector((state) => state.auth);
  const { isShowModal } = useSelector((state) => state.alert);

  const timerRef = useRef(null);
  const countdownIntervalRef = useRef(null);

  const [remainingTime, setRemainingTime] = useState(0);

  //     
  const extendSession = () => {
    const newExpiresAt = Date.now() + SESSION_DURATION;
    dispatch(
      setLogin({
        token: token,
        expiresIn: SESSION_DURATION / 1000,
      })
    );
    dispatch(setPopupReset()); //
  };

  //   &   
  const resetTimers = () => {
    if (!expiresAt) return;

    const now = Date.now();
    const timeout = expiresAt - now;

    //  
    if (timerRef.current) clearTimeout(timerRef.current);
    if (countdownIntervalRef.current)
      clearInterval(countdownIntervalRef.current);

    //    
    if (timeout <= 0) {
      dispatch(setLogout());
      return;
    }

    //   
    setRemainingTime(timeout);

    //    
    countdownIntervalRef.current = setInterval(() => {
      const timeLeft = expiresAt - Date.now();
      setRemainingTime(timeLeft);

      // 10    
      if (timeLeft <= 10000 && timeLeft > 0 && !isShowModal) {
        // setShowAlert(true);
        dispatch(
          setIsModalAlert({
            title: "",
            context: "  . \n ?",
            btnName: "",
            isShowModal: true,
            onConfirm: () => extendSession(),
          })
        );
      }

      if (timeLeft <= 0) {
        clearInterval(countdownIntervalRef.current);
        dispatch(setLogout());
      }
    }, 1000);

    //   
    timerRef.current = setTimeout(() => {
      dispatch(setLogout());
      dispatch(setPopupReset());
    }, timeout);
  };

  // expiresAt    
  useEffect(() => {
    resetTimers();
  }, [expiresAt]);

  //    
  useEffect(() => {
    return () => {
      if (timerRef.current) clearTimeout(timerRef.current);
      if (countdownIntervalRef.current)
        clearInterval(countdownIntervalRef.current);
    };
  }, []);

  return (
    <>
      {/*    */}
      {token && (
        <div id="Toast">
          <div className="toast-group">
            <div className="toast-item warn">
              <p>
                 <b>{formatTime(remainingTime)}</b>  .
              </p>
            </div>
          </div>
        </div>
      )}
      <ModalAlert />
    </>
  );
};

export default SessionManager;
